
.card-carousel {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    height: 200px;
    margin-top: 200px;
    align-items: center;
    position: relative;
  }
  
  .card {
    background: #7a7a7a;
    border-radius: 8px;
    border: 1px solid #6a6a6a;
    text-align: center;
    padding: 30px;
    box-shadow: 0 10px 5px rgba(0, 0, 0, 0.1);
    width: 310px;
    height: 200px;
    transition: all 0.75s ease;
    opacity: 0;
    position: absolute;
    transform: scale(0.25) translateY(50px);
  }
  
  .card h2,
  .card p {
    line-height: 1.5;
    margin: 0;
  }
  
  .card.active {
    opacity: 1;
    transform: scale(1) translateY(0);
    box-shadow: 0 30px 20px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }
  
  .card.next {
    opacity: 1;
    z-index: 0;
  }
  
  .card.prev {
    transform: scale(1.1) translateY(-50px);
    z-index: 2;
    opacity: 0;
    visibility: hidden;
  }